@use '../../styles/variables' as v;

.tabsWrapper {
  display: flex;
  width: calc(100% - 50px);
  height: 40px;
  position: absolute;
  top: 0;
  right: 0;
  background-color: v.$Dark-color-primary_bar;
  transition: width linear 0.25s;

  &.sidebar {
    width: calc(100% - 250px);
    transition: width linear 0.25s;
  }
}

.tabsWrapper.light-theme {
  background-color: v.$Light-color-primary_bar;

  .linkWrapper {
    color: black;
    background-color: v.$Light-color-tabs;
    border-right: 1px solid white;

    &.active {
      background-color: white;
    }

    .close {
      color: v.$Light-color-text;
      &:hover {
        background-color: rgb(218, 218, 218);
      }
    }
  }
}

.linkWrapper {
  position: relative;
  display: flex;
  align-items: center;
  border-right: 1px solid v.$Dark-color-background;
  color: v.$Dark-color-text;
  background-color: v.$Dark-color-tabs;

  &.active {
    color: whitesmoke;
    background-color: v.$Dark-color-background;
    transition: background-color 0.1s;
  }

  .close {
    opacity: 0;
    transition: opacity 0.1s;
    background: none;
    border: none;
    width: fit-content;
    font-size: 0.7rem;
    color: v.$Dark-color-text;
    position: relative;
    left: -10px;
    padding: 2px 3px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s;

    &:hover {
      background-color: rgb(102, 100, 100);
      transition: background-color 0.2s;
    }
  }

  &:hover .close {
    opacity: 1;
    transition: opacity 0.2s;
  }
}
.tab {
  display: flex;
  grid-column-gap: 10px;
  align-items: center;
  padding: 0 20px 0 10px;
}
